body {
  margin: 0;
  padding: 0;
}

html {
  --text-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-of-type {
  margin: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active, .nav__link:hover {
  color: var(--text-color);
}

.is-active:after {
  content: "";
  color: var(--text-color);
  background-color: var(--text-color);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.img {
  width: 40px;
  height: 40px;
}
